import { WatchlistGroups } from "@rp/react/tanstack-api/discovery-panel-config"
import {
  EnhancedCqsItemSuggestion,
  isCompanySuggestion,
  isETFSuggestion,
} from "@rp/react/tanstack-api/suggestion"
import { RPTooltip } from "@rp/react/web/common/base-components"
import { EllipsisTooltip } from "@rp/react/web/common/composite-components"
import {
  Box,
  Checkbox,
  Skeleton,
  TableCell,
  TableRow,
  Typography,
  capitalize,
} from "@rp/react/web/mui/core"

type Props = {
  suggestion: EnhancedCqsItemSuggestion
  selected: boolean
  disabled?: boolean
  included: boolean
  onChange: (key: string, value: boolean) => void
  categoryFilter?: string
  isLoading: boolean
  fields: WatchlistGroups["fields"]
  hasFilter?: boolean
}

const WatchlistSuggestionItem = ({
  suggestion,
  selected,
  included,
  onChange,
  categoryFilter,
  isLoading,
  disabled,
  fields,
}: Props) => {
  const { key, name, description, typeDesc, type } = suggestion
  return (
    <TableRow>
      <TableCell width={50}>
        <RPTooltip
          title={`This ${type === "rp_topic" ? "topic" : "entity"} is already in your watchlist`}
          disableFocusListener={!included}
          disableHoverListener={!included}
        >
          <Box>
            <Checkbox
              disabled={disabled || included}
              name={name}
              checked={selected}
              sx={{ visibility: isLoading ? "hidden" : "visible" }}
              onChange={(_, selected) => onChange(key, selected)}
              className="watchlist-editor-suggestion-item-checkbox"
            />
          </Box>
        </RPTooltip>
      </TableCell>
      <TableCell sx={{ maxWidth: 300 }}>
        {isLoading ? (
          <Box sx={{ flexGrow: 1 }}>
            <Skeleton width={189} height={15} variant="rectangular" sx={{ maxWidth: "100%" }} />
            <Skeleton
              width={147}
              height={15}
              variant="rectangular"
              sx={{ mt: 0.5, maxWidth: "70%" }}
            />
          </Box>
        ) : (
          <Box sx={{ display: "flex", flexDirection: "column" }}>
            <Box sx={{ display: "flex", alignItems: "center" }}>
              <EllipsisTooltip tooltipText={name} typographyProps={{ variant: "body2" }}>
                {name}
              </EllipsisTooltip>
              {isCompanySuggestion(suggestion) || isETFSuggestion(suggestion) ? (
                <Typography color="blue.200" variant="caption" sx={{ ml: 0.5 }}>
                  {suggestion.ticker}
                </Typography>
              ) : null}
            </Box>
            {description ? (
              <EllipsisTooltip
                tooltipText={description}
                typographyProps={{ color: "rgba(255, 255, 255, 0.6)", variant: "body2" }}
                tooltipPlacement="bottom"
                vertical
              >
                {description}
              </EllipsisTooltip>
            ) : null}
          </Box>
        )}
      </TableCell>
      {categoryFilter ? (
        fields.map(({ key }) => {
          const displayValue = suggestion[key]
          return (
            <TableCell key={key}>
              {isLoading ? (
                <Skeleton height={15} variant="rectangular" />
              ) : (
                <Typography
                  variant="body2"
                  textAlign={typeof displayValue === "string" ? "left" : "center"}
                >
                  {typeof displayValue === "string"
                    ? displayValue.split("-").map(capitalize).join(" ")
                    : "-"}
                </Typography>
              )}
            </TableCell>
          )
        })
      ) : (
        <TableCell sx={{ maxWidth: 300 }}>
          {isLoading ? (
            <Skeleton height={15} variant="rectangular" />
          ) : (
            <Typography variant="body2">{typeDesc} </Typography>
          )}
        </TableCell>
      )}
    </TableRow>
  )
}

export { WatchlistSuggestionItem }
export type { Props }
